/* TYPOGRAPHY */
/* Google Fonts */
/* VARIABLES */
header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
}
@media all and (max-width: 640px) {
  header {
    position: initial;
    background: var(--bs-primary);
  }
}

#navbar-top {
  padding-top: 16px;
  max-width: 1920px;
  margin: auto;
}

.user-logged-in header {
  top: 79px;
}

.btn {
  text-transform: uppercase;
  letter-spacing: 0.8px;
  padding-left: 30px;
  padding-right: 30px;
}

.main-content {
  padding: 0;
}

.node__content {
  margin-top: 0;
}

.user-logged-in nav.tabs {
  max-width: 50%;
  margin: auto;
}
.user-logged-in nav.tabs ul.nav-tabs {
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 0.9px;
}

.site-footer {
  padding-top: 64px;
}

.site-footer__bottom .region-footer-fifth {
  max-width: 1920px;
  margin: auto;
}

.banner {
  min-height: 690px;
  height: 100%;
  background-color: #eee;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-end;
  padding: 64px;
  max-width: 1920px;
  margin: auto;
}
.banner.left_align {
  align-items: flex-start;
  text-align: left;
}
.banner.right_align {
  align-items: flex-end;
  text-align: right;
}
.banner.center_align {
  align-items: center;
  text-align: center;
}
.banner h1 {
  font-weight: 600;
}

.list-component {
  min-height: 690px;
  height: 100%;
  padding: 60px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  max-width: 1920px;
  margin: auto;
  justify-content: center;
  background-color: #ccc;
}
.list-component .listed-components {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  justify-content: center;
}
.list-component .listed-components > .item {
  text-align: center;
}
.list-component .listed-components > .item:hover .thumbnail {
  overflow: hidden;
  transform: scale(1.1);
}
.list-component .listed-components .thumbnail {
  max-width: 250px;
  margin-left: auto;
  margin-right: auto;
}
@media all and (max-width: 640px) {
  .list-component .listed-components {
    width: 100%;
  }
}
.list-component .subtitle {
  padding-bottom: 32px;
  margin-bottom: 32px;
  position: relative;
}
.list-component .subtitle:after {
  content: "";
  position: absolute;
  width: 80px;
  height: 8px;
  background: #fff;
  bottom: 0;
  left: calc(50% - 40px);
}
.list-component h2, .list-component h3 {
  text-transform: uppercase;
  font-weight: 600;
}
.list-component h2 {
  margin-bottom: 32px;
  text-align: center;
}
.list-component h3 {
  margin-bottom: 16px;
}
@media (min-width: 560px) {
  .list-component .blazy--field {
    max-width: 250px;
  }
}

@media (min-width: 560px) {
  .node .field--type-image {
    float: none;
    margin: 0;
  }
}

.thumbnail {
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 32px;
  transition: transform 0.2s, -webkit-transform 0.2s;
}
.thumbnail:hover {
  transform: scale(1.1);
}

.qoute_wrapper {
  min-height: 690px;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  overflow: hidden;
}
.qoute_wrapper.fullscreen {
  max-width: 1920px;
  margin: auto;
  align-content: flex-end;
}
.qoute_wrapper.fullscreen .qoute-container {
  width: 100%;
}
.qoute_wrapper.fullscreen .blazy--field-qoute-image {
  position: absolute;
  width: 100%;
  z-index: 1;
  height: 100%;
  top: 0;
  left: 0;
}
.qoute_wrapper.fullscreen .blazy--field-qoute-image .media--blazy {
  padding-bottom: 0 !important;
  position: relative;
  width: 100%;
  height: 100%;
}
.qoute_wrapper.fullscreen .qoute_description {
  position: relative;
  z-index: 2;
  width: 100%;
  margin-bottom: 64px;
  padding: 32px;
  color: #fff;
  mix-blend-mode: hard-light;
}
.qoute_wrapper.side_by_side {
  max-height: 50vh;
  min-height: 0;
}
@media all and (max-width: 640px) {
  .qoute_wrapper.side_by_side {
    max-height: unset;
  }
}
.qoute_wrapper.side_by_side .qoute-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1920px;
  margin: auto;
}
.qoute_wrapper.side_by_side .qoute-container > * {
  flex: 1;
}
.qoute_wrapper.side_by_side .qoute_description {
  padding: 64px;
  color: #fff;
}
@media all and (max-width: 1000px) {
  .qoute_wrapper.side_by_side .qoute_description {
    padding: 32px;
  }
}
.qoute_wrapper.side_by_side .blazy--field-qoute-image .media--blazy {
  padding-bottom: 0 !important;
  position: relative;
  width: 100%;
  height: 100%;
}
@media all and (max-width: 640px) {
  .qoute_wrapper.side_by_side .blazy--field-qoute-image .media--blazy {
    padding-bottom: 75% !important;
  }
}

.qoute_description.orange {
  position: relative;
}
.qoute_description.orange:after {
  content: "";
  background: rgba(255, 98, 0, 0.8);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}

blockquote {
  font-size: 24px;
  background-image: url(../images/qoute.svg);
  background-repeat: no-repeat;
  padding-left: 42px !important;
}

.sixty_forty, .equal {
  display: flex;
  flex-wrap: wrap;
}

.sixty_forty .content-left {
  flex: 0 40%;
  padding-right: 32px;
}
.sixty_forty .content-right {
  flex: 0 60%;
  padding-left: 32px;
}

.equal {
  flex-wrap: nowrap;
  padding: 64px;
  max-width: 1920px;
  margin: auto;
}
@media all and (max-width: 640px) {
  .equal {
    display: block;
  }
}
.equal .content-left, .equal .content-right {
  flex: 0 50%;
  width: 50%;
}
.equal .content-left {
  padding-right: 32px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media all and (max-width: 640px) {
  .equal .content-left {
    padding-right: 0;
  }
}
.equal .content-right {
  padding-left: 32px;
}
@media all and (max-width: 640px) {
  .equal .content-right {
    padding-left: 0;
  }
}

.simple.sixty_forty {
  padding: 64px;
  max-width: 1920px;
  margin: auto;
}

.complex {
  min-height: 690px;
  height: 100%;
  padding: 64px;
  max-width: 1920px;
  margin: auto;
}
@media all and (max-width: 640px) {
  .complex {
    padding: 32px;
  }
}
.complex .content-right {
  overflow: hidden;
}

.content-right .at-title {
  display: none;
}
.content-right .at-inner {
  max-width: 760px;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.73);
  padding: 50px 50px 30px;
  float: right;
  border-top: 5px solid #ff6200;
  border-bottom: 5px solid #ff6200;
  width: 100%;
}
@media all and (max-width: 991px) {
  .content-right .at-inner {
    max-width: 100%;
    margin-top: 32px;
  }
}
.content-right .at fieldset legend {
  color: #0035a0;
  padding: 0;
}
.content-right .at label {
  color: #0035a0;
}
.content-right .at form .label-amount {
  width: 22%;
  height: auto;
  margin-right: 4%;
  float: left;
  color: #fff;
  font-size: 24px;
  background-image: none;
  padding: 8px;
  margin-bottom: 15px;
  letter-spacing: -0.03em;
  line-height: 1.428571429;
  display: block;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
  text-shadow: -1px -1px 1px rgba(38, 116, 52, 0.1);
  font-weight: 400;
  white-space: nowrap;
  position: relative;
  z-index: 1;
  color: #0035a0;
}
.content-right .at form .label-amount input[type=radio]:checked + a, .content-right .at form .label-amount:hover > a {
  background-color: #ffede1;
  border-color: #0035a0;
}
.content-right .at form .label-amount > a {
  position: absolute;
  top: 0;
  right: 0;
  height: 50px;
  left: 0;
  display: block;
  background-color: transparent;
  border-radius: 4px;
  z-index: -1;
  border: 2px solid #0035a0;
}
.content-right .at input[type=checkbox] + span::before {
  border: 1px solid #0035a0;
}
.content-right .at input[type=checkbox]:checked + span:before {
  background-color: #228ae6;
  border-color: #228ae6;
}
.content-right .at input[type=checkbox]:hover + span:before {
  border-color: #1c7cd6;
}
.content-right .at .btn-at-primary {
  font-size: 1.2rem;
  letter-spacing: 2px;
  border-radius: 0;
}
.content-right .at-fields {
  position: relative;
}